import { Button, Form, Drawer, InputNumber, Radio } from "@arco-design/web-react";
import { useDispatch, useSelector } from "react-redux";
import { getLoan } from "../../api";
import { RootState } from "../../store";
import { hideCalculator, showResult } from "../../store/houseSlice";
const FormItem = Form.Item;

export default function PersonInfo() {
    const [form] = Form.useForm();

    const visible = useSelector((state: RootState) => state.house.calculatorVisible);

    const selectedHouse = useSelector((state: RootState) => state.house.selectedHouse);

    const dispatch = useDispatch();

    return <Drawer 
            style={{width: 800}} 
            visible={visible} 
            footer={null} 
            onCancel={() => {dispatch(hideCalculator())}}
            title=""
            >
        <Form form={form} onSubmit={async (values) => {
            const houseList = selectedHouse.map(_ => ({ ..._, totalPrice: parseFloat(`${_.totalPrice}0000`) }))
            if(values.totalPrice) {
                houseList.push({ 
                    totalPrice: values.totalPrice, 
                    name:'', 
                    unitPrice: '0', 
                    square: '0', 
                    type: '0', 
                    date: '', 
                    toward: '0', 
                    floor: '0',  
                    rate: '0',
                    id: 0,
                    url: '',
                    note: '',
                    community: '',
                    property: '',
                    developer: ''
                })
            }
            getLoan({
                ...values,
                houseList,
            }).then((response) => {
                dispatch(showResult(response));
            })
        }}>
            <FormItem label="()" field="totalPrice">
                <InputNumber suffix=""/>
            </FormItem>
            <FormItem label="" field="currentPrincipal">
                <InputNumber suffix=""/>
            </FormItem>
            <FormItem label="" field="income">
                <InputNumber suffix=""/>
            </FormItem>
            <FormItem label="" field="rent">
                <InputNumber suffix=""/>
            </FormItem>
            <FormItem label="" field="outcome">
                <InputNumber suffix=""/>
            </FormItem>
            <FormItem label="" field="syInterestRate">
                <InputNumber suffix=""/>
            </FormItem>
            <FormItem label="" field="gjjInterestRate">
                <InputNumber suffix=""/>
            </FormItem>
            <FormItem label="" field="firstPay">
                <InputNumber suffix=""/>
            </FormItem>
            <FormItem label="" field="gjjLoan">
                <InputNumber suffix=""/>
            </FormItem>
            <FormItem label="" field="syYear" defaultValue="20">
                <InputNumber suffix=""/>
            </FormItem>
            <FormItem label="" field="gjjYear" defaultValue="20">
                <InputNumber suffix=""/>
            </FormItem>
            <FormItem wrapperCol={{ offset: 5 }}>
                <Button type='primary' htmlType='submit' style={{ marginRight: 24 }}>
                
                </Button>
                <Button
                    onClick={() => {
                        form.resetFields();
                    }}
                >
                    
                </Button>
            </FormItem>
        </Form>
    </Drawer>
}